import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";
import {
  createGiftCard,
  getAllTemplates,
  getGiftCards,
  redeemGiftCard,
} from "../api/giftCardApis";

const useGiftCards = () => {
  return useQuery({
    queryKey: ["giftCards"],
    queryFn: getGiftCards,
    staleTime: 1000 * 60 * 5,
  });
};

const useCreateGiftCard = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: createGiftCard,
    onSuccess: () => {
      queryClient.invalidateQueries(["giftCards"]);
    },
  });
};

const useRedeemGiftCard = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: redeemGiftCard,
    onSuccess: () => {
      queryClient.invalidateQueries(["giftCards"]);
    },
  });
};

const useGiftCardTemplates = () => {
  return useQuery({
    queryKey: ["giftCard-templates"],
    queryFn: getAllTemplates,
    staleTime: 1000 * 60 * 5,
  });
};

export {
  useGiftCards,
  useCreateGiftCard,
  useRedeemGiftCard,
  useGiftCardTemplates,
};
