import axiosInstance from "./axiosInstance";

const getGiftCards = async () => {
  const response = await axiosInstance.get("/getGiftCardByUser");
  return response.data;
};

const createGiftCard = async (cardData) => {
  const response = await axiosInstance.post("/createGiftCard", cardData);
  return response.data;
};

const redeemGiftCard = async (code) => {
  const response = await axiosInstance.post("/redeemGiftCard", { code });
  return response.data;
};

const getAllTemplates = async () => {
  const response = await axiosInstance.get("/getAllTemplates");
  return response.data?.allTemplates;
};

export { getGiftCards, createGiftCard, redeemGiftCard, getAllTemplates };
