import { useState } from "react";
import Button from "../../components/Button";
import InputBox from "../../components/InputBox";
import TextareaBox from "../../components/TextareaBox";
import useForm from "../../hooks/useForm";
import { useUser } from "../../contexts/userContext";
import { useWallet } from "../../contexts/walletContext";
import {
  useCreateGiftCard,
  useGiftCardTemplates,
} from "../../services/giftCardServices";
import { motion } from "framer-motion";
import { useNavigate } from "react-router-dom";
import { useToast } from "../../contexts/toastContext";
import Spinner from "../../components/Spinner";

const AmountBtn = ({ amount, isActive = false, onClick }) => {
  return (
    <button
      type="button"
      onClick={onClick}
      className={`w-16 h-7 border border-primeRed rounded-md flex items-center justify-center gap-1 p-1 text-sm font-secondary font-medium text-primeRed ${
        isActive ? "bg-lightGray" : "bg-white"
      }`}
    >
      <span>₹</span>
      <p>{amount}</p>
    </button>
  );
};

const CreateCard = () => {
  const navigate = useNavigate();
  const toast = useToast();
  const { user } = useUser();
  const { getBalance } = useWallet();
  const {
    data: templates = [],
    isLoading: templatesLoading,
    error: templatesError,
  } = useGiftCardTemplates();
  const createMutation = useCreateGiftCard();
  const [amount, setAmount] = useState(null);
  const [customAmount, setCustomAmount] = useState(null);
  const [selectedTemplate, setSelectedTemplate] = useState(null);

  const handleCustomAmount = (value) => {
    setCustomAmount(value);
    setAmount(null);
  };

  const handleAmount = (value) => {
    setCustomAmount(null);
    setAmount(value);
  };

  const onSubmitHandler = async (cardData) => {
    const { recipientName, recipientEmail, recipientPhone, message } = cardData;

    // Client side validations
    const warnings = [];

    if (!customAmount && !amount) {
      warnings.push("Please select an amount");
    }
    if (!selectedTemplate) {
      warnings.push("Please select a gift card template");
    }

    if (warnings.length > 0) {
      warnings.forEach((warning) => toast.error(warning));
      return;
    }

    const payload = {
      value: amount || customAmount,
      Recipient_Information: {
        name: recipientName,
        email: recipientEmail,
        phoneNumber: recipientPhone,
      },
      templateId: selectedTemplate._id,
      message,
    };

    createMutation.mutate(payload, {
      onSuccess: async () => {
        await getBalance();
        toast.success("Gift card created successfully");
        navigate("/account/gift-card");
        clearForm();
      },
      onError: (error) => {
        console.error(error);
        toast.error(error?.response?.data?.message);
      },
    });
  };

  const { formData, handleChange, handleSubmit, clearForm } = useForm(
    {},
    onSubmitHandler
  );

  return (
    <motion.main
      className="flex-1"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <div className="bg-white p-6 rounded-md lg:shadow-md">
        <h2 className="text-2xl font-semibold mb-4">Create Gift Card</h2>

        <section className="flex justify-center flex-wrap gap-8">
          <div className="LEFT space-y-4">
            <div className="w-80 h-48 bg-lightGray rounded-md flex justify-center items-center overflow-hidden">
              {selectedTemplate ? (
                <img
                  src={selectedTemplate.template}
                  alt={selectedTemplate.title}
                  className="size-full"
                />
              ) : (
                <p className="text-primary">No template selected</p>
              )}
            </div>
            <div className="w-80 bg-white rounded-md border border-secondary p-4 text-sm space-y-2 overflow-x-hidden">
              <p>From: {user.fullName}</p>
              <p>To: {formData.recipientName}</p>
              <p>Expires: {new Date().toLocaleDateString()}</p>
              <p>
                {formData.message || "Enjoy this gift card on Finafid.com!"}
              </p>
            </div>
          </div>

          <div className="RIGHT flex-grow space-y-4">
            <p className="font-secondary font-medium leading-none">
              Choose a design
            </p>
            <div className="GIFT-CARDS-CONTAINER w-full min-h-20 max-h-52 overflow-y-scroll">
              <ul className="flex gap-4 flex-wrap p-1">
                {templatesLoading ? (
                  <div className="w-full flex justify-center">
                    <Spinner />
                  </div>
                ) : templatesError ? (
                  <p className="w-full text-center text-primary">
                    templatesError.message
                  </p>
                ) : templates.length === 0 ? (
                  <p className="w-full text-center text-primary">
                    No Templates available.
                  </p>
                ) : (
                  templates.map((template, index) => (
                    <li
                      onClick={() => setSelectedTemplate(template)}
                      key={index}
                      className={`w-20 h-14 flex-shrink-0 ${
                        selectedTemplate &&
                        selectedTemplate._id === template._id &&
                        "outline outline-primeRed"
                      }`}
                    >
                      <img
                        src={template.template}
                        alt={template.title}
                        className="size-full"
                      />
                    </li>
                  ))
                )}
              </ul>
            </div>

            <p className="font-secondary font-medium leading-none">
              Choose an amount
            </p>
            <div className="flex gap-4 flex-wrap items-center">
              {Array.from([1000, 2000, 3000, 4000]).map((value, index) => (
                <AmountBtn
                  key={index}
                  isActive={value === amount}
                  amount={value}
                  onClick={() => handleAmount(value)}
                />
              ))}

              <div className="CUSTOM-AMOUNT">
                <p className="text-[9px]">Custom amount:</p>
                <div className="w-20 h-7 flex border border-primeRed rounded-md bg-white overflow-hidden">
                  <span className="w-max h-full flex items-center justify-center text-sm text-primeRed px-2 border-r border-primeRed">
                    ₹
                  </span>
                  <input
                    type="number"
                    name="customAmount"
                    value={customAmount || ""}
                    onChange={(e) => handleCustomAmount(e.target.value)}
                    className="[appearance:textfield] [&::-webkit-outer-spin-button]:appearance-none [&::-webkit-inner-spin-button]:appearance-none outline-none w-full px-2 text-sm font-secondary font-medium text-primeRed"
                  />
                </div>
                <p className="text-[9px]">(Maximum ₹ 10,000)</p>
              </div>
            </div>

            <p className="font-secondary font-medium leading-none">
              Recipient Details
            </p>
            <div className="space-y-2">
              <p className="font-medium text-xs">Enter the details</p>
              <form className="space-y-3" onSubmit={handleSubmit}>
                <InputBox
                  type="tel"
                  name="recipientPhone"
                  value={formData.recipientPhone}
                  onChange={handleChange}
                  placeholder="recipient mobile number"
                  className="border-b-2 border-dark py-1 text-sm placeholder:text-secondary"
                />
                <InputBox
                  type="email"
                  name="recipientEmail"
                  value={formData.recipientEmail}
                  onChange={handleChange}
                  placeholder="recipient email"
                  className="border-b-2 border-dark py-1 text-sm placeholder:text-secondary"
                />
                <InputBox
                  type="text"
                  name="recipientName"
                  value={formData.recipientName}
                  onChange={handleChange}
                  placeholder="recipient name"
                  className="border-b-2 border-dark py-1 text-sm placeholder:text-secondary"
                />
              </form>
            </div>
            <p className="font-secondary font-medium">
              Add an optional message
            </p>
            <div>
              <form onSubmit={handleSubmit}>
                <TextareaBox
                  name="message"
                  value={formData.message}
                  onChange={handleChange}
                  maxLength={150}
                  placeholder="Enter optional message"
                  className="resize-none h-36 text-sm placeholder:text-secondary border-b-2 border-dark"
                />
                <Button
                  disabled={createMutation.isPending}
                  className="ml-auto mt-8 px-4 py-2 rounded-md font-secondary text-sm"
                >
                  create
                </Button>
              </form>
            </div>
          </div>
        </section>
      </div>
    </motion.main>
  );
};

export default CreateCard;
